        <el-dropdown-item command="busyIndex_manage">繁忙指数记录管理</el-dropdown-item> 
        <el-dropdown-item command="trainCount_manage">列车数量记录管理</el-dropdown-item>
        <el-dropdown-item command="intervel_runtime_manage">区间运行时管理</el-dropdown-item>

