<script>
export default {
  data() {
    return {
      avatarSrc: 'https://img.zcool.cn/community/015a605f45ac07a80120a8218319a8.jpg@1280w_1l_2o_100sh.jpg' // 你的人物头像路径
    };
  },
  methods: {
    handleCommand1(command) {
      if (command === 'railway_analysis') {
        this.$router.push('/railway_analysis')
      } else if (command === 'test') {
        // 展示还未开发弹窗
        this.$router.push('/test')
      } else if (command === 'map') {
        this.$emit('showRank')
      }else if (command === 'chart') {
        this.$router.push('/chart')
      }
    },
    handleCommand2(command) {
      if (command === 'station_manage') {
        this.$router.push('/station_manage')
      } else if (command === 'section_manage') {
        this.$router.push('/section_manage')
      } else if (command === 'line_manage') {
        this.$router.push('/line_manage')
      } else if (command === 'trainRecord_manage') {
        this.$router.push('/trainRecord_manage')
      } else if (command === 'busyIndex_manage') {
        this.$router.push('/busyIndex_manage')
      } else if (command === 'trainCount_manage') {
        this.$router.push('/trainCount_manage')
      }else if (command === 'intervel_runtime_manage') {
        this.$router.push('/intervel_runtime_manage')
      }
    },
    handleCommand3(command) {
      this.$router.push('/user_manage')
    },
    handleCommand4(command) {
      if (command === 'busy_distribution') {
        this.$router.push('/busy_distribution')
      }else if (command === 'time_series_analysis') {
        this.$router.push('/time_series_analysis')
      }else if (command === 'station_frequency_analysis') {
        this.$router.push('/station_frequency_analysis')
      }else if (command === 'train_type_and_count') {
        this.$router.push('/train_type_and_count')
      }else if (command === 'running_time_distribution') {
        this.$router.push('/running_time_distribution')
      }else if( command === 'interval_busy_details'){
        this.$router.push('/interval_busy_details')
      }
    },
    handleLogout() {
      this.$router.push('/login')
    },
    register(){
      this.$router.push('/register')
    },
    handleCommand(command) {
      if (command === 'logout') {
        this.handleLogout();
      } else if (command === 'register') {
        this.register();
      }
    },

  }
}
</script>
